import Vue from 'vue'
import Router from 'vue-router'
import PCMusicstore from './../components/pc_components/pc_musicstore'
import PCMymusic from './../components/pc_components/pc_mymusic'
import PCClient from './../components/pc_components/pc_client'
import PCMusicnumber from './../components/pc_components/pc_musicnumber'
import PCVip from './../components/pc_components/pc_vip'
//
import PCMSdefault from './../components/pc_components/pc_mstore_component/pc_ms_default/pc_ms_default.vue'
import PCMSsinger from './../components/pc_components/pc_mstore_component/pc_ms_singer/pc_ms_singer.vue'
import PCMSalbum from './../components/pc_components/pc_mstore_component/pc_ms_album.vue'
import PCMSlist from './../components/pc_components/pc_mstore_component/pc_ms_list.vue'
import PCMSclassify from './../components/pc_components/pc_mstore_component/pc_ms_classify.vue'
import PCMSradio from './../components/pc_components/pc_mstore_component/pc_ms_radio.vue'
import PCMSmv from './../components/pc_components/pc_mstore_component/pc_ms_mv.vue'
import PCMSnumberalbum from './../components/pc_components/pc_mstore_component/pc_ms_numberalbum.vue'
import PCSingerDetail from './../components/pc_components/pc_singerDetail/pc_singerDetail.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/PCMusicstore/PCMSdefault'
    },{
      path: '/PCMusicstore',
      redirect: '/PCMusicstore/PCMSdefault'
    }
    ,{
      path: '/PCMusicstore',
      name: 'PCMusicstore',
      component: PCMusicstore,
      children:[
        {
          path: '/PCMusicstore/PCMSdefault',
          name: 'PCMSdefault',
          component: PCMSdefault
        },{
          path: '/PCMusicstore/PCMSsinger',
          name: 'PCMSsinger',
          component: PCMSsinger
        },{
          path: '/PCMusicstore/PCMSalbum',
          name: 'PCMSalbum',
          component: PCMSalbum
        },{
          path: '/PCMusicstore/PCMSlist',
          name: 'PCMSlist',
          component: PCMSlist
        },{
          path: '/PCMusicstore/PCMSclassify',
          name: 'PCMSclassify',
          component: PCMSclassify
        },{
          path: '/PCMusicstore/PCMSradio',
          name: 'PCMSradio',
          component: PCMSradio
        },{
          path: '/PCMusicstore/PCMSmv',
          name: 'PCMSmv',
          component: PCMSmv
        },{
          path: '/PCMusicstore/PCMSnumberalbum',
          name: 'PCMSnumberalbum',
          component: PCMSnumberalbum
        },{
          path: '/PCMusicstore/PCSingerDetail/:singerid',
          name: 'PCMSnumberalbum',
          component: PCSingerDetail
        }]
    },{
      path: '/PCMymusic',
      name: 'PCMymusic',
      component: PCMymusic
    },{
      path: '/PCClient',
      name: 'PCClient',
      component: PCClient
    },{
      path: '/PCMusicnumber',
      name: 'PCMusicnumber',
      component: PCMusicnumber
    },{
      path: '/PCVip',
      name: 'PCVip',
      component: PCVip
    }    
  ]
})



